"use client";
import React, { useState, useEffect } from "react";
import { Menu, X } from "lucide-react";
import Link from "next/link";
import CtaButton from "./CtaButton";
import { usePathname } from "next/navigation";

const Header = () => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [isClient, setIsClient] = useState(false);
  const pathname = usePathname(); // Get current path from Next.js

  useEffect(() => {
    setIsClient(true);
  }, []);

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50);
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const navLinks = [
    { name: "Our Ink & Passion", href: "/about" },
    { name: "Our Capabilities", href: "/services" },
    { name: "The Proof Gallery", href: "/portfolio" },
  ];

  return (
    <nav
      className={`fixed! w-full! z-50! transition-all duration-300 ${
        isScrolled
          ? "backdrop-blur-sm bg-white/30 py-4"
          : "bg-transparent py-6 text-neutral-900"
      }`}
    >
      <div className="max-w-11/12 mx-auto px-4 sm:px-6 lg:px-8 relative">
        <div className="flex justify-between items-center">
          <div className="flex items-center space-x-2">
            <div>
              <Link
                href="/"
                className={`text-xl font-bold ${
                  isScrolled ? "text-black!" : "text-white!"
                }`}
              >
                LOGO
              </Link>
            </div>
          </div>

          {/* Desktop Navigation */}
          <div className="hidden md:flex space-x-8">
            {navLinks.map((link) => {
              const isActive = isClient && pathname === link.href;
              return (
                <div key={link.name} className="group">
                  <Link
                    href={link.href}
                    className={`relative font-medium text-xl transition-colors ${
                      isScrolled
                        ? isActive
                          ? "text-black!"
                          : "text-gray-900!"
                        : isActive
                        ? "text-white!"
                        : "text-white!"
                    }`}
                  >
                    {link.name}

                    {/* Vibrating underline with rounded edges */}
                    <span
                      className={`vibrating-line absolute left-0 -bottom-1 w-full h-[4px] rounded-full transition-all duration-300 ${
                        isScrolled ? "bg-black!" : "bg-white!"
                      } ${
                        isActive
                          ? "opacity-100"
                          : "opacity-0 group-hover:opacity-100"
                      }`}
                    />
                  </Link>

                </div>
              );
            })}
          </div>

          <div className="hidden md:flex">
            <CtaButton isScrolled={isScrolled} />
          </div>

          {/* Mobile Menu Button */}
          <div className="md:hidden flex items-center gap-2">
            <button onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}>
              {isMobileMenuOpen ? (
                <X className={isScrolled ? "text-gray-900!" : "text-white!"} />
              ) : (
                <Menu className={isScrolled ? "text-gray-900!" : "text-white!"} />
              )}
            </button>
          </div>
        </div>

        {/* Mobile Menu */}
        {isMobileMenuOpen && (
          <div className="md:hidden mt-4 bg-white rounded-lg shadow-xl p-4">
            {navLinks.map((link) => (
              <Link
                key={link.name}
                href={link.href}
                className="block py-3 text-gray-700 hover:text-blue-600 font-medium border-b border-gray-100 last:border-0"
                onClick={() => {
                  setIsMobileMenuOpen(false);
                }}
              >
                {link.name}
              </Link>
            ))}
            <Link
              href="#contact"
              className="block mt-4 bg-linear-to-r from-blue-600 to-purple-600 text-white px-6 py-3 rounded-full font-medium text-center"
              onClick={() => setIsMobileMenuOpen(false)}
            >
              Get in Touch
            </Link>
          </div>
        )}
      </div>
    </nav>
  );
};

export default Header;